ul {
  list-style: none; /*убираем маркеры списка*/
  margin: 0; /*убираем верхнее и нижнее поле, равное 1em*/
  padding-left: 0; /*убираем левый отступ, равный 40px*/
}
a {text-decoration: none; /*убираем подчеркивание текста ссылок*/}

* {
  padding: 0;
  margin: 0;
  /*box-sizing: border-box;*/
}


.scrollbar::-webkit-scrollbar {
  width: 8px;

}
.scrollbar::-webkit-scrollbar-track {
  background: #2F2F37;
}
.scrollbar::-webkit-scrollbar-thumb {
  background: #8585AD;
  /*border: 3px solid #2F2F37;*/
  border-radius: 6px;
}

.white {
  color: #F2F2F3;
}
.dark-gray {
  color: #8585AD;
}
.error {
  text-align: center;
  color: brown;
  margin-top: 200px;
  font-size: 60px;
}

.auth__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  margin-bottom: 80px;
}
.auth__section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 140px;
  /*height: calc(100vh - 88px);*/
}
.auth__title {
  color: #F2F2F3;
}
.auth__footer-text {
  color: #8585AD;
}
.auth__link {
  color: #4C4CFF;
}
