.wrapper {
    background: #1C1C21;
}
.header {
    max-width: 1280px;
    margin: 0 auto;
}

.list {
    display: flex;
    width: 100%;
}
.logo {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.nav {
    position: relative;
    display: flex;
    justify-content: space-between;
}
.item {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
}
